only-child {\r\n    display: none !important;\r\n  }\r\n}\r\n.swiper-pagination-bullet-active {\r\n  opacity: var(--swiper-pagination-bullet-opacity, 1);\r\n  background: var(--swiper-pagination-color, var(--swiper-theme-color));\r\n}\r\n\r\n.swiper-vertical > .swiper-pagination-bullets,\r\n.swiper-pagination-vertical.swiper-pagination-bullets {\r\n  right: 10px;\r\n  top: 50%;\r\n  transform: translate3d(0px, -50%, 0);\r\n  .swiper-pagination-bullet {\r\n    margin: var(--swiper-pagination-bullet-vertical-gap, 6px) 0;\r\n    display: block;\r\n  }\r\n  &.swiper-pagination-bullets-dynamic {\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n    width: 8px;\r\n    .swiper-pagination-bullet {\r\n      display: inline-block;\r\n      transition: 200ms transform, 200ms top;\r\n    }\r\n  }\r\n}\r\n.swiper-horizontal > .swiper-pagination-bullets,\r\n.swiper-pagination-horizontal.swiper-pagination-bullets {\r\n  .swiper-pagination-bullet {\r\n    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 4px);\r\n  }\r\n  &.swiper-pagination-bullets-dynamic {\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    white-space: nowrap;\r\n    .swiper-pagination-bullet {\r\n      transition: 200ms transform, 200ms left;\r\n    }\r\n  }\r\n}\r\n.swiper-horizontal.swiper-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {\r\n  transition: 200ms transform, 200ms right;\r\n}\r\n/* Progress */\r\n.swiper-pagination-progressbar {\r\n  background: rgba(0, 0, 0, 0.25);\r\n  position: absolute;\r\n  .swiper-pagination-progressbar-fill {\r\n    background: var(--swiper-pagination-color, var(--swiper-theme-color));\r\n    position: absolute;\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    transform: scale(0);\r\n    transform-origin: left top;\r\n  }\r\n  .swiper-rtl & .swiper-pagination-progressbar-fill {\r\n    transform-origin: right top;\r\n  }\r\n  .swiper-horizontal > &,\r\n  &.swiper-pagination-horizontal,\r\n  .swiper-vertical > &.swiper-pagination-progressbar-opposite,\r\n  &.swiper-pagination-vertical.swiper-pagination-progressbar-opposite {\r\n    width: 100%;\r\n    height: 4px;\r\n    left: 0;\r\n    top: 0;\r\n  }\r\n  .swiper-vertical > &,\r\n  &.swiper-pagination-vertical,\r\n  .swiper-horizontal > &.swiper-pagination-progressbar-opposite,\r\n  &.swiper-pagination-horizontal.swiper-pagination-progressbar-opposite {\r\n    width: 4px;\r\n    height: 100%;\r\n    left: 0;\r\n    top: 0;\r\n  }\r\n}\r\n.swiper-pagination-lock {\r\n  display: none;\r\n}\r\n","@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Light';\n  font-style: normal;\n  font-weight: 300;\n  src:\n    font-path('DINPro-Light.woff2') format('woff2'),\n    font-path('DINPro-Light.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro';\n  font-style: normal;\n  font-weight: 400;\n  src:\n    font-path('DINPro.woff2') format('woff2'),\n    font-path('DINPro.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Medium';\n  font-style: normal;\n  font-weight: 500;\n  src:\n    font-path('DINPro-Medium.woff2') format('woff2'),\n    font-path('DINPro-Medium.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Bold';\n  font-style: normal;\n  font-weight: 700;\n  src:\n    font-path('DINPro-Bold.woff2') format('woff2'),\n    font-path('DINPro-Bold.woff2') format('woff');\n}\n","/* COLORS */\n$black: #1c1c1c;\n$white: #fff;\n$light: #f8f8f8;\n$grey: #acacac;\n$primary: #ed1c24;\n$error: #c8362e;\n$success: #519735;\n$washed-red: #d59c9e;\n$night-blue: #323975;\n\n/* OVERLAY */\n$overlay-black: rgba(0, 0, 0, 0.2);\n\n$text-colors: (\n  'black' : $black,\n  'white': $white,\n  'grey': $grey,\n  'bs-red': $primary,\n  'light': $light,\n  'night-blue': $night-blue,\n);\n\n// Fonts Weight\n$weight-light: 300;\n$weight-regular: 400;\n$weight-medium: 500;\n$weight-bold: 700;\n\n// Font sizes\n$size-1: rem(46);\n$size-2: rem(40);\n$size-3: rem(36);\n$size-4: rem(26);\n$size-5: rem(20);\n$size-6: rem(16);\n$size-7: rem(14);\n\n$sizes: $size-1 $size-2 $size-3 $size-4 $size-5 $size-6 $size-7;\n\n// Shadow, find more smooth shadow: https://s.muz.li/MWNjYTY2Yjk4\n$shadow: 0 0.9px 2.6px rgba(0, 0, 0, 0.02),\n  0 3.1px 5.1px rgba(0, 0, 0, 0.03),\n  0 14px 14px rgba(0, 0, 0, 0.05)\n;\n\n// Easing\n$easing-smooth: cubic-bezier(0.5, 0, 0, 1);\n\n// Transition\n$transition: 0.2s ease;\n$transition-smooth: 0.5s $easing-smooth;\n\n// MEDIA QUERIES\n$breakpoints: (\n  // FOR IE\n  ie: '(-ms-high-contrast: active), (-ms-high-contrast: none)',\n  max-hugescreen:'(max-width:1599px)', min-hugescreen:'(min-width:1600px)',\n  max-fullhd: '(max-width:1407px)', min-fullhd: '(min-width:1408px)',\n  max-widescreen: '(max-width:1215px)', min-widescreen: '(min-width:1216px)',\n  max-desktop: '(max-width:1023px)', min-desktop: '(min-width:1024px)',\n  max-mobile: '(max-width:768px)', min-mobile: '(min-width:769px)',\n  max-small-mobile: '(max-width:540px)', min-small-mobile: '(max-width:540px)',\n  max-tiny: '(max-width:370px)', min-tiny: '(max-width:370px)',\n) !default;\n\n",".text {\n  @each $name, $color in $text-colors {\n    &-#{$name} {\n      color: $color !important;\n    }\n  }\n}\n\n.bg {\n  @each $name, $color in $text-colors {\n    &-#{$name} {\n      background-color: $color !important;\n    }\n  }\n}\n\nhtml {\n  background-color: $night-blue;\n}\n\na {\n  color: inherit;\n\n  &:hover {\n    color: $primary;\n  }\n}\n\nem {\n  color: $primary;\n  font-style: normal;\n}\n\nstrong {\n  color: inherit;\n  font-family: DINPro-Medium;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font: 15px/1.5rem DINPro, sans-serif;\n}\n\nh1 {\n  font-size: 48px;\n  line-height: 53px;\n  @include mq('min-desktop') {\n    font-size: 96px;\n    line-height: 106.8px;\n  }\n}\n\nh2 {\n  font-size: 30px;\n  line-height: 35px;\n  @include mq('min-desktop') {\n    font-size: 48px;\n    line-height: 53px;\n  }\n}\n\nh3 {\n  font-size: 28px;\n  line-height: 40px;\n  @include mq('min-desktop') {\n    font-size: 36px;\n    line-height: 52px;\n  }\n}\n\nh4 {\n  font-size: 20px;\n  line-height: 25px;\n  @include mq('min-desktop') {\n    font-size: 24px;\n    line-height: 36px;\n  }\n}\n\nh5 {\n  font-size: 16px;\n  line-height: 24px;\n}\n\nh6 {\n  font-size: 14px;\n  line-height: 24px;\n}\n\n\n.text-bold {\n  font-family: DINPro-Bold;\n}\n\n.text-upper {\n  text-transform: uppercase;\n}\n\n.text-center {\n  text-align: center;\n}\n\n.hide {\n  opacity: 0;\n  transition: opacity 1s;\n}\n\n.relative {\n  position: relative;\n}\n\n.button {\n  background-color: $white;\n  border: 0.1rem solid $primary;\n  border-radius: 0.2rem;\n  color: $primary;\n  cursor: pointer;\n  padding: 0.5rem 1rem;\n  text-decoration: none;\n\n  &:hover {\n    background: $primary;\n    color: $white;\n  }\n}\n\n.container-layout {\n  margin: 0 auto;\n  max-width: rem(1160);\n  width: 100%;\n\n  &.is-medium {\n    max-width: rem(960);\n  }\n\n  &.is-small {\n    max-width: rem(760);\n  }\n\n  &.is-smaller {\n    max-width: rem(540);\n  }\n\n  &.is-tiny {\n    max-width: rem(460);\n  }\n}\n","@use 'sass:math';\n\n@mixin mq($breakpoint-name) {\n  $breakpoint-name: unquote($breakpoint-name);\n  @if map-has-key($breakpoints, $breakpoint-name) {\n    $query: map-get($breakpoints, $breakpoint-name);\n    @media all and #{$query} {\n      @content;\n    }\n  } @else {\n    @warn '#{$mq-name} is not a value defined in the 'breakpoints' map.';\n  }\n}\n\n// BACKGROUND GRADIENT\n// https://robots.thoughtbot.com/controlling-color-with-sass-color-functions\n// https://glennmccomb.com/articles/creating-smooth-sequential-animations-with-sass/\n@mixin bg-gradient($directionTo: top, $colorStart: black, $colorEnd: white , $upTo: 100%) {\n  background: linear-gradient(to $directionTo, $colorStart 0%, $colorEnd $upTo);\n}\n\n@mixin pseudo($display: block, $pos: absolute, $content: '') {\n  content: $content;\n  display: $display;\n  position: $pos;\n}\n\n@mixin responsive-ratio($x, $y, $pseudo: true) {\n  $padding: unquote(math.div($y, $x) * 100 + '%');\n  @if $pseudo {\n    &:before {\n      @include pseudo($pos: relative);\n      padding-top: $padding;\n      width: 100%;\n    }\n  } @else {\n    > .ratio:first-child {\n      display: block;\n      padding-top: $padding;\n    }\n  }\n}\n"],"sourceRoot":""}]);
